.newsletter {
  width: 65%;
  /* height: 40vh; */
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: auto;
  padding: 0px 140px;
  margin-top: 100px;
  margin-bottom: 150px;
  background: linear-gradient(180deg, #fde1ff 0%, #e1ffea22 60%);
  gap: 30px;
}
.newsletter h1 {
  margin-top: 30px;
  color: #454545;
  font-size: 35px;
  font-weight: 600;
}
.newsletter p {
  color: #454545;
  font-size: 15px;
}
.newsletter div {
  display: flex;
  align-items: center;
  justify-content: space-between;
  background: #fff;
  width: 630px;
  height: 50px;
  border-radius: 80px;
  border: 1px solid #e3e3e3;
}
.newsletter input {
  width: 500px;
  margin-left: 30px;
  border: none;
  outline: none;
  color: #616161;
  font-family: "poppins";
  font-size: 16px;
}
.newsletter div button {
  width: 210px;
  height: 50px;
  border-radius: 80px;
  background: #000;
  color: #fff;
  font-size: 16px;
  cursor: pointer;
  font-weight: 500;
}
